const swiper = new Swiper('.swiper', {
  direction: 'horizontal',
  loop: true,
  slidesPerView: 'auto',
  spaceBetween: 5,
  breakpoints: {
    600: {
      slidesPerView: 3,
    },
    1100: {
      slidesPerView: 4,
      spaceBetween: 30,
    }
  },
});

const modal = document.querySelector('.modal')
const cross = document.querySelector('.cross')
const burger = document.querySelector('.burger-hover')

function openModal() {
  modal.classList.toggle('open')
  if(modal.classList.contains('open')) {
    document.body.style.cssText='overflow:hidden;'
  }
}

function closeModal(){
  if(modal.classList.contains('open')) {
    modal.classList.remove('open')
    document.body.style.cssText='overflow:visible;'

  }
}

burger.addEventListener('click', openModal)
cross.addEventListener('click', closeModal)


document.getElementById("eye").addEventListener("click", function(e){
  const passwordShow = document.getElementById("old-password");
  if(passwordShow.getAttribute("type")=="password"){
    passwordShow.setAttribute("type","text");
  } else {
    passwordShow.setAttribute("type","password");
  }
});